:root {
  --background: #ffffff;
  --foreground: #171717;
  --maroon: #8a0933;
  --maroon-light: #a61e4d;
  --maroon-dark: #6d0a2a;
  --white: #ffffff;
  --gray: #888888;
  --light-gray: #f8fafc;
  --dark-gray: #1e293b;
  --gradient-primary: linear-gradient(
    135deg,
    #8a0933 0%,
    #a61e4d 50%,
    #c2255c 100%
  );
  --gradient-secondary: linear-gradient(
    135deg,
    #1e293b 0%,
    #334155 50%,
    #475569 100%
  );
  --gradient-accent: linear-gradient(
    135deg,
    #3b82f6 0%,
    #8b5cf6 50%,
    #ec4899 100%
  );
  --text-primary: #0f172a;
  --text-secondary: #475569;
  --text-muted: #94a3b8;
  --text-on-dark: #f8fafc;
  --text-on-dark-secondary: rgba(
    248,
    250,
    252,
    0.8
  );
  --text-on-dark-muted: rgba(248, 250, 252, 0.6);
  --animation-duration-fast: 0.2s;
  --animation-duration-normal: 0.3s;
  --animation-duration-slow: 0.5s;
  --animation-ease: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-ease-bounce: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
html {
  background: var(--background);
  min-height: 100%;
  font-family: var(--font-family-sans);
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-family-sans);
  min-height: 100vh;
  position: relative;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
div,
span {
  font-family: var(--font-family-sans);
}
* {
  font-family: var(--font-family-sans) !important;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 0 20px rgba(138, 9, 51, 0.3);
  }
  50% {
    box-shadow: 0 0 30px rgba(138, 9, 51, 0.6);
  }
}
@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.animate-fade-in-up {
  animation: fadeInUp var(--animation-duration-slow) var(--animation-ease)
    forwards;
}
.animate-fade-in-left {
  animation: fadeInLeft var(--animation-duration-slow) var(--animation-ease)
    forwards;
}
.animate-fade-in-right {
  animation: fadeInRight var(--animation-duration-slow) var(--animation-ease)
    forwards;
}
.animate-scale-in {
  animation: scaleIn var(--animation-duration-normal)
    var(--animation-ease-bounce) forwards;
}
.animate-float {
  animation: float 3s ease-in-out infinite;
}
.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}
.animate-gradient {
  background-size: 200% 200%;
  animation: gradient-shift 3s ease infinite;
}
.animate-delay-100 {
  animation-delay: 0.1s;
}
.animate-delay-200 {
  animation-delay: 0.2s;
}
.animate-delay-300 {
  animation-delay: 0.3s;
}
.animate-delay-400 {
  animation-delay: 0.4s;
}
.animate-delay-500 {
  animation-delay: 0.5s;
}
.glass {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.glass-dark {
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.card-hover {
  transition: all var(--animation-duration-normal) var(--animation-ease);
}
.card-hover:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}
.gradient-text {
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.btn-modern {
  position: relative;
  overflow: hidden;
  transition: all var(--animation-duration-normal) var(--animation-ease);
}
.btn-modern::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  transition: left var(--animation-duration-slow) var(--animation-ease);
}
.btn-modern:hover::before {
  left: 100%;
}
html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--light-gray);
}
::-webkit-scrollbar-thumb {
  background: var(--maroon);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--maroon-dark);
}
@keyframes number-flip {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(-90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}
@keyframes number-bounce {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}
@keyframes glow-pulse {
  0%,
  100% {
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
  }
  50% {
    box-shadow: 0 0 30px rgba(255, 255, 255, 0.6);
  }
}
.animate-number-flip {
  animation: number-flip 0.3s ease-in-out;
}
.animate-number-bounce {
  animation: number-bounce 0.3s ease-in-out;
}
.animate-glow-pulse {
  animation: glow-pulse 0.3s ease-in-out;
}
* {
  box-sizing: border-box;
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
*:focus {
  outline: 2px solid var(--maroon);
  outline-offset: 2px;
}
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: 2px solid var(--maroon);
  outline-offset: 2px;
}
@media (prefers-contrast: high) {
  .gradient-text {
    background: none;
    color: var(--maroon);
    -webkit-text-fill-color: var(--maroon);
  }
}
